/* Contact Us Page Styles */
.contact-us-page {
  min-height: 100vh;
  background: white;
  position: relative;
}
.contact-us-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50vh;
  background: #F5EEE4;
  z-index: 0;
}

/* Header Section */
.contact-header {
  padding: 4rem 0 2rem 0;
  text-align: center;
  position: relative;
  z-index: 1;
  background: #F5EEE4;
}

.header-content {
  max-width: 800px;
  margin: 10 auto;
}

.contact-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
  margin-top: 1rem;
  line-height: 1.2 !important;
}

.contact-subtitle {
  font-size: 1.2rem !important;
  color: #5A6C7D !important;
  font-weight: 400 !important;
  line-height: 1.5 !important;
}

/* Main Content Card */
.contact-main-section {
  padding: 0 0 8rem 0;
  position: relative;
  z-index: 1;
  background: white;
}

.contact-card {
  background: white !important;
  border-radius: 20px !important;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1) !important;
  overflow: hidden !important;
  min-height: 600px;
}

/* Contact Information Section (Left Column) */
.contact-info-section {
  padding: 3rem;
  height: 100%;
  display: flex;
  margin: 1.2rem;
  border-radius: 0.5rem;
  flex-direction: column;
  background: #F5EEE4;
}

.team-image-container {
  margin-bottom: 2rem;
}

.team-image-placeholder {
  width: 100%;
  height: 250px;
  background: transparent;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  overflow: hidden;
}

.contact-details {
  flex: 1;
}

.contact-info-title {
  font-size: 1.8rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 2rem !important;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  gap: 1rem;
}

.contact-icon {
  color: #70A6B2 !important;
  font-size: 1.5rem !important;
  margin-top: 0.2rem;
  flex-shrink: 0;
}

.contact-text {
  font-size: 1rem !important;
  color: #2C3E50 !important;
  line-height: 1.6 !important;
  flex: 1;
}

/* Contact Form Section (Right Column) */
.contact-form-section {
  padding: 3rem;
  height: 100%;
  background: #FAFAFA;
  display: flex;
  flex-direction: column;
}

.form-title {
  font-size: 1.8rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.form-description {
  font-size: 1rem !important;
  color: #5A6C7D !important;
  margin-bottom: 2rem !important;
  line-height: 1.6 !important;
}

.contact-form {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.form-field {
  margin-bottom: 1rem !important;
}

.form-field .MuiOutlinedInput-root {
  background: white !important;
  border-radius: 8px !important;
}

.form-field .MuiOutlinedInput-notchedOutline {
  border-color: #E0E0E0 !important;
}

.form-field .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: #70A6B2 !important;
}

.form-field .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: #70A6B2 !important;
  border-width: 2px !important;
}

.form-field .MuiInputLabel-root {
  color: #5A6C7D !important;
}

.form-field .MuiInputLabel-root.Mui-focused {
  color: #70A6B2 !important;
}

/* Phone Number Prefix */
.phone-prefix {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-right: 0.5rem;
  padding: 0 0.5rem;
  border-right: 1px solid #E0E0E0;
}

.flag-icon {
  width: 20px;
  height: 15px;
  border-radius: 2px;
}

.country-code {
  font-weight: 500 !important;
  color: #2C3E50 !important;
  font-size: 0.9rem !important;
}

/* Submit Button */
.submit-button-container {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.submit-button {
  background: #FF6B35 !important;
  color: white !important;
  font-size: 1.1rem !important;
  font-weight: 600 !important;
  padding: 12px 40px !important;
  border-radius: 25px !important;
  text-transform: none !important;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3) !important;
  transition: all 0.3s ease !important;
}

.submit-button:hover {
  background: #E55A2B !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4) !important;
}

/* Bottom White Space */
.contact-bottom-space {
  height: 6rem;
  background: white;
  width: 100%;
}

/* Floating Chat Button */
.floating-chat {
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 1000;
}

.chat-button {
  background: #2C3E50 !important;
  color: white !important;
  width: 60px !important;
  height: 60px !important;
  border-radius: 50% !important;
  min-width: 60px !important;
  box-shadow: 0 4px 15px rgba(44, 62, 80, 0.3) !important;
  transition: all 0.3s ease !important;
}

.chat-button:hover {
  background: #1A252F !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(44, 62, 80, 0.4) !important;
}

.chat-button .MuiSvgIcon-root {
  font-size: 1.5rem !important;
}

/* Left Edge Line */
.contact-us-page::before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  width: 4px;
  height: 100vh;
  background: #70A6B2;
  z-index: 1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .contact-title {
    font-size: 2.2rem !important;
  }
  
  .contact-subtitle {
    font-size: 1rem !important;
  }
  
  .contact-card {
    margin: 0 1rem;
  }
  
  .contact-info-section,
  .contact-form-section {
    padding: 2rem;
  }
  
  .team-image-placeholder {
    height: 200px;
  }
  
  .contact-info-title,
  .form-title {
    font-size: 1.5rem !important;
  }
  
  .floating-chat {
    right: 20px;
    bottom: 20px;
  }
  
  .chat-button {
    width: 50px !important;
    height: 50px !important;
    min-width: 50px !important;
  }
}

@media (max-width: 480px) {
  .contact-header {
    padding: 2rem 0 1rem 0;
  }
  
  .contact-title {
    font-size: 1.8rem !important;
  }
  
  .contact-main-section {
    padding: 0 0 2rem 0;
  }
  
  .contact-info-section,
  .contact-form-section {
    padding: 1.5rem;
  }
  
  .team-image-placeholder {
    height: 150px;
  }
}
